import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
  display: flex;
  flex-direction: column;
  background-color: #CF6E36;
`;

const SiteName = styled.h1`
 font-family: KakaoBold;
 font-size: 50pt;
 color: #FAF4C0;
 margin-top: 10px;
 margin-left: 20px;
`;

const BlogDescription = styled.div`
  font-size: 12pt;
  font-family: KakaoRegular;
  color: #FFFFFF;
  margin-left: 20px;
  margin-top: 5px;
`;


const Schedule = styled.div`
  display: flex;
  flex-direction: row;
  margin-left: 20px;
`;

const ScheduleTitle = styled.div`
  font-size: 12pt;
  font-family: KakaoBold;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: 2px yellow underline;
  margin-left: 20px;
  margin-top: 30px;
`;


const ScheduleBox = styled.div`
  background-color: #FFFFFF;
  color: #000000;
  padding: 20px;
  border-radius: 50px;
  margin: 20px 10px;
  font-family: KakaoBold;
  font-size: 10pt;
  font-weight: bold;
`;

const Copybutton = styled.button`
  background-color: #FFFFFF;
  color: #000000;
  font-family: KakaoBold;
  font-size: 12pt;
  border-radius: 50px;
  padding: 2opx;
  margin: 20px 10px;
  font-weight: bold;
  text-shadow: 2px 2px 2px gray;
`;

const Posttitle = styled.div`
  font-size: 12pt;
  font-family: KakaoBold;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: 2px yellow underline;
  margin-left: 20px;
`;

const Posts = styled.div`
  display: flex;
  margin-top: 10px;
  flex-direction: column;
  background-color: #FFFFFF;
  margin-left: 20px;
  border-radius: 10px;
  align-items: center;
`;

const PostBox = styled.div`
  margin-top: 10px;
  background-color: #F2F2F2;
  margin-left: 20px;
  margin-top: 10px;
  border-radius: 10px;
`;

const PostBoxTitle = styled.div`
  font-family: KakaoBold;
  font-size: 12pt;
  font-weight: bold;
  margin-left: 20px;
  margin-top: 10px;
`;

const PostItemBox = styled.div`
  background-color: #FFFFFF;
  margin-left: 20px;
  border-radius: 10px;
`;

const PostItemTitle = styled.div`
  font-family: KakaoBold;
  font-size: 12pt;
  font-weight: bold;
  margin-left: 20px;
  margin-top: 10px;
`;

const PostItem = styled.div`
  font-family: KakaoRegular;
  font-size: 10pt;
  color: #000000;
  margin-left: 20px;
`;

const PostItemImage = styled.img`
  width: 150px;
  height: 100px;
  margin-top: 10px;
  display: block;
  margin-left: auto;
  margin-right: auto;
`;

const SummaryItem = styled.div`
  font-family: KakaoBold;
  font-size: 12pt;
  font-weight: bold;
  text-align: center;
`;

const Tags = styled.div`
  display: flex;
  margin-top: 20px;
  margin-left: 20px;
  flex-direction: row;
`;

const TagBox = styled.div`
  background-color: #1EFFF1;
  color: #000000;
  font-family: KakaoRegular;
  font-size: 10pt;
  padding: 10px 20px;
  border-radius: 50px;
  margin-left: 10px;
`;


function IndividualBlogPost() {
  return (

    <Container>
      <div>
        <div>
          <SiteName>Trip Again</SiteName>
          <BlogDescription>  </BlogDescription>
        </div>
        <div>

        <ScheduleTitle>SCHEDULE</ScheduleTitle>
        <Schedule>
        <ScheduleBox>Europe 2019.07.07 ~ 2019.08.13      </ScheduleBox>
        <Copybutton> </Copybutton>
        </Schedule>
        </div>
        <div>
        <Posttitle>POST</Posttitle>
        <Posts>
          <PostBox>
            <PostBoxTitle>1  - </PostBoxTitle>
            <PostItemBox>
              <PostItemTitle>   -  12,000</PostItemTitle>
              <PostItem>
                  ...       .
               3     , 8    9   .
               0 20  , 0 55  !
              </PostItem>
            <PostItemImage src = "Incheon.jpg" alt = "Incheon" />
            </PostItemBox>
            <SummaryItem> / 13  /  1,400,000</SummaryItem>
            <PostItemBox>
              <PostItemTitle>   -  14,000</PostItemTitle>
              <PostItem>
                12         .         !
              </PostItem>
            </PostItemBox>
            <Tags>
              <TagBox>#</TagBox>
              <TagBox>#</TagBox>
            </Tags>
          </PostBox>
        </Posts>
      </div>
      </div>
    </Container>
  );
};

export default IndividualBlogPost;